@mixin phone {
  @media only screen and (max-width: 768px) {
    @content;
  }
}

.loader {
  margin: auto;
  width: 50%;
  max-width: 400px;
  min-width: 150px;
  position: relative;
  top: 50%;
  left: 30%;
  transform: translate(-50%, -50%);
  font-size: 60px;
  color: white;

  > p {
    font-size: 2rem;
  }
  // transform: rotate(360deg);
  // animation: spinner 1s linear infinite;

  @include phone {
    font-size: 40px;
  }
}
